#header-cont{
    display: block;
    border-bottom: 1px solid #cccccc;
    padding-top: 15px;
    padding-bottom: 15px;
    background: #fff;
    position: fixed;
    width: 100%;
}

#header-inner{
    height: 35px;
    padding-right: 2.5em;
    padding-left: 2.5em;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}